@use "./assets/scss/base/reset";
@use "./assets/scss/abstracts/colors" as *;

body {
	width: 100%;
	height: 100vh;
}
#root {
	width: 100%;
	height: 100%;
}
.mainNav {
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	height: 10vh;
	padding: 2rem;
}
.container {
	display: flex;
	flex-direction: column;
	width: 100%;
	padding: 3rem;
	.countriesWrapper {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		justify-items: center;
		align-items: center;
		gap: 2rem;
		.card {
			width: 85%;
			height: 50vh;
			box-shadow: 0px 3px 32px 12px rgba(0, 0, 0, 0.35);
			.cardContent {
				display: flex;
				flex-direction: column;
				gap: 1rem;
				padding: 2rem;
			}
		}
		img {
			width: 100%;
			border-bottom: 1px solid rgba(255, 255, 255, 0.103);
		}
	}
}
.mainForm {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 2rem;
	label {
		width: 80%;
		input[type="text"] {
			width: 35%;
			height: 5vh;
		}
	}
}
